body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.margin-3 {
  margin: 3%;
}

.margin-bottom-3 {
    margin-bottom: 3%;
}

.margin-5 {
    margin: 5%;
}

.margin-bottom-5 {
    margin-bottom: 5%;
}

.font-20 {
    font-size: 20px;
}

.flex-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.box {
    width: 98%;
    margin: 1% 1% 3%;
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .box {
        width: 31%;
        margin: 1%;
    }
}


